$MainColor : #ff0000;

.red {
    color : $MainColor;
}

@mixin 함수() {
    background-color: #eeeeee;
    padding: 1rem;
    border-radius: 0.5rem;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include 함수()
}

.my-alert2 {
    @extend .my-alert;
    background-color: #ffe591;
}

.my-alert p {
    margin-bottom: 0;
}

.loading {
    margin-top: 20px;
    background-color: #ffe591;
    width: 100%;
    padding: 1rem;

    p {
        margin-bottom: 0;
    }
}

